.TermsWrapper {
  width: 100vw;
  overflow: hidden;
  height: auto;
  margin-top: 8vh;
  background-color: whitesmoke;
  animation: simpleAppear 650ms;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.TermsWrapper__colored {
  width: 100vw;
  height: 20vh;
  background-image: radial-gradient(circle farthest-corner at 0% 50%, rgba(255, 1, 1, 0.15), transparent 47%), radial-gradient(circle farthest-corner at 40% 130%, rgba(122, 167, 255, 0.41), transparent 58%);
  background-color: #1d1d1d;
}
.TermsWrapper__textContent {
  width: 80vh;
  height: 165vh;
  background-color: white;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  transform: translateY(-12vh);
  border-radius: 2vh;
}
@media screen and (orientation: portrait) {
  .TermsWrapper__textContent {
    width: 90vw;
    height: 230vh;
  }
}
.TermsWrapper__textContent__title {
  font-size: 4.5vh;
  padding-left: 5vh;
  padding-top: 8vh;
  font-weight: 600;
}
@media screen and (orientation: portrait) {
  .TermsWrapper__textContent__title {
    font-size: 3.75vh;
    padding-left: 3vh;
  }
}
.TermsWrapper__textContent__subtitle {
  padding-left: 5vh;
  padding-top: 1vh;
  font-size: 2.1vh;
  font-weight: 400;
  color: rgb(109, 109, 109);
}
@media screen and (orientation: portrait) {
  .TermsWrapper__textContent__subtitle {
    padding-left: 3vh;
  }
}
.TermsWrapper__textContent .additionalPadding {
  padding-top: 4vh;
  padding-bottom: 4vh;
}
.TermsWrapper__textContent__paragraph {
  padding-top: 1vh;
  padding-bottom: 1vh;
  padding-left: 5vh;
  max-width: 70vh;
  width: 70vh;
  font-size: 1.9vh;
  font-weight: 400;
  color: #1d1d1d;
}
@media screen and (orientation: portrait) {
  .TermsWrapper__textContent__paragraph {
    width: 80vw;
    max-width: 80vw;
    padding-left: 3vh;
  }
}
.TermsWrapper__textContent__sectionTitle {
  padding-left: 5vh;
  font-weight: 600;
  font-size: 3vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  margin-top: 5vh;
}
@media screen and (orientation: portrait) {
  .TermsWrapper__textContent__sectionTitle {
    padding-left: 3vh;
  }
}