.navbar {
  width: 100vw;
  height: 8vh;
  background-color: white;
  animation: simpleAppear 650ms;
  display: flex;
  justify-content: space-around;
  position: fixed;
  z-index: 1000;
  overflow-x: hidden;
}
@media screen and (orientation: portrait) {
  .navbar {
    justify-content: space-between;
  }
  .navbar__left {
    transform: translateX(10vw);
  }
}
.navbar__right {
  width: 28vw;
  height: 8vh;
  display: flex;
  justify-content: space-around;
  align-content: center;
  width: 20vw;
}
@media screen and (orientation: portrait) {
  .navbar__right {
    justify-content: flex-end;
  }
}
.navbar__right__hamburgerButtons {
  z-index: -1;
  width: 100vw;
  height: 30vh;
  background-color: white;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-content: center;
  transform: translateY(8vh);
  transition: transform 250ms;
  will-change: transform;
}
.navbar__right__hamburgerButtons__a {
  text-decoration: none;
}
.navbar__right__hamburgerButtons__a__btn {
  display: block;
  width: auto;
  height: 4vh;
  margin-top: 2vh;
  background-color: transparent;
  border: none;
  color: #777;
  font-weight: 700;
  font-size: 1.6vh;
  transition: background-color 250ms, color 250ms;
  will-change: background-color, color;
  cursor: pointer;
  width: 75%;
  margin-left: 12.5%;
  font-size: 1.9vh;
}
.navbar__right__hamburgerButtons__a__btn:hover {
  color: #333;
}
.navbar__right__hamburgerButtons__a__btn--bg {
  display: block;
  width: auto;
  height: 4vh;
  margin-top: 2vh;
  background-color: transparent;
  border: none;
  color: #777;
  font-weight: 700;
  font-size: 1.6vh;
  transition: background-color 250ms, color 250ms;
  will-change: background-color, color;
  cursor: pointer;
  background-color: #333;
  color: white;
  padding-left: 1.5vw;
  padding-right: 1.5vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border-radius: 1vh;
  width: 75%;
  margin-left: 12.5%;
  font-size: 1.9vh;
}
.navbar__right__hamburgerButtons__a__btn--bg:hover {
  background-color: rgb(82, 82, 82);
}
.navbar__right .switched {
  transform: translateY(-38vh);
}
.navbar__right__hamburgerMenu {
  width: 8vh;
  height: 7vh;
  margin-top: 0.5vh;
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  flex-direction: column;
  cursor: pointer;
}
.navbar__right__hamburgerMenu__stripe {
  background-color: #333;
  width: 6vh;
  height: 0.8vh;
}
.navbar__right__a {
  text-decoration: none;
}
.navbar__right__a__btn {
  display: block;
  width: auto;
  height: 4vh;
  margin-top: 2vh;
  background-color: transparent;
  border: none;
  color: #777;
  font-weight: 700;
  font-size: 1.6vh;
  transition: background-color 250ms, color 250ms;
  will-change: background-color, color;
  cursor: pointer;
}
.navbar__right__a__btn:hover {
  color: #333;
}
.navbar__right__a__btn:hover a {
  text-decoration: none;
  color: #333;
}
.navbar__right__a__btn--bg {
  display: block;
  width: auto;
  height: 4vh;
  margin-top: 2vh;
  background-color: transparent;
  border: none;
  color: #777;
  font-weight: 700;
  font-size: 1.6vh;
  transition: background-color 250ms, color 250ms;
  will-change: background-color, color;
  cursor: pointer;
  background-color: #333;
  color: white;
  padding-left: 1.5vw;
  padding-right: 1.5vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border-radius: 1vh;
}
.navbar__right__a__btn--bg:hover {
  background-color: rgb(82, 82, 82);
}
.navbar__left {
  width: 28vw;
  height: 8vh;
  display: flex;
  justify-content: space-around;
  align-content: center;
}
.navbar__left__form {
  display: flex;
  justify-content: center;
  align-content: center;
  height: 4vh;
  width: auto;
  transform: translateY(2vh);
  border-radius: 1vh;
  background-color: rgb(241, 241, 241);
}
.navbar__left__form__input {
  width: 14vw;
  border: none;
  height: 4vh;
  outline: none;
  font-size: 1.6vh;
  background-color: rgb(241, 241, 241);
  border-radius: 1vh;
}
.navbar__left__form__input::placeholder {
  color: gray;
}
.navbar__left__form__icon {
  height: 4vh;
  width: 4vh;
  border: none;
}
.navbar__left__form__icon__image {
  height: 2vh;
  width: 2vh;
  margin-left: 1vh;
  margin-top: 1vh;
}
.navbar__left__logo {
  display: block;
  margin-top: 2.25vh;
  height: 3.5vh;
  cursor: pointer;
}