.LicensesWrapper {
  width: 100vw;
  height: 100vh;
  background-color: whitesmoke;
  overflow: hidden;
  margin-top: 8vh;
  animation: simpleAppear 650ms;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 20vw;
  padding-top: 8vh;
  padding-bottom: 8vh;
}
@media screen and (orientation: portrait) {
  .LicensesWrapper {
    padding-left: 5vw;
    height: 160vh;
  }
}
.LicensesWrapper__title {
  font-size: 5vh;
  font-weight: 600;
}
.LicensesWrapper__description {
  font-size: 1.85vh;
  max-width: 25vw;
  width: 25vw;
  color: #444;
}
@media screen and (orientation: portrait) {
  .LicensesWrapper__description {
    width: 80vw;
    max-width: 80vw;
    margin-top: 2vh;
  }
}
.LicensesWrapper__info {
  height: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
}
@media screen and (orientation: portrait) {
  .LicensesWrapper__info {
    flex-direction: column;
    height: 140vh;
    justify-content: space-around;
  }
}
.LicensesWrapper__info__images {
  width: 60vh;
}
@media screen and (orientation: portrait) {
  .LicensesWrapper__info__images {
    width: 80vw;
  }
}
.LicensesWrapper__info__images__img {
  width: 22vh;
  margin-left: 1vw;
  margin-right: 1vw;
  margin-top: 1vh;
  margin-bottom: 1vh;
  transform: translateX(-1vw);
}
@media screen and (orientation: portrait) {
  .LicensesWrapper__info__images__img {
    width: 26vh;
    margin-left: 0vw;
    margin-right: 0vw;
    margin-top: 3vh;
    margin-bottom: 3vh;
    transform: translateX(-2.5vw);
  }
}
.LicensesWrapper__info__text {
  width: 50vw;
  transform: translateX(-15vh);
}
@media screen and (orientation: portrait) {
  .LicensesWrapper__info__text {
    transform: translateX(0vw);
    width: 95vw;
  }
}
.LicensesWrapper__info__text__title {
  font-size: 4.25vh;
  margin-top: 1vh;
  margin-bottom: 1vh;
}
.LicensesWrapper__info__text__link {
  color: gray;
  margin-top: 1vh;
  margin-bottom: 1vh;
  font-size: 2vh;
}