.InstructionsWrapper {
  width: 100vw;
  height: 160vh;
  background-color: whitesmoke;
  margin-top: 8vh;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 20vw;
  padding-top: 8vh;
  padding-bottom: 8vh;
}
@media screen and (orientation: portrait) {
  .InstructionsWrapper {
    padding-left: 5vw;
    height: 175vh;
  }
}
.InstructionsWrapper__image {
  margin-top: 3vh;
  width: 70vh;
}
@media screen and (orientation: portrait) {
  .InstructionsWrapper__image {
    width: 40vh;
  }
}
.InstructionsWrapper__title {
  font-size: 5vh;
  font-weight: 600;
}
.InstructionsWrapper__subtitle {
  font-size: 2.9vh;
  margin-top: 1vh;
  margin-bottom: 1vh;
  font-weight: 600;
  color: #333;
}
.InstructionsWrapper__description {
  font-size: 1.95vh;
  max-width: 35vw;
  width: 35vw;
  color: #444;
}
@media screen and (orientation: portrait) {
  .InstructionsWrapper__description {
    width: 80vw;
    max-width: 80vw;
    margin-top: 2vh;
  }
}