.ContactWrapper {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  padding-top: 15vh;
  padding-bottom: 10vh;
  overflow-x: hidden;
  background-image: radial-gradient(circle farthest-corner at 0% 50%, rgba(255, 1, 1, 0.15), transparent 47%), radial-gradient(circle farthest-corner at 40% 130%, rgba(122, 167, 255, 0.41), transparent 58%);
  background-color: #1d1d1d;
}
.ContactWrapper__paragraph {
  font-weight: 600;
  color: #a8a8a8;
  font-size: 1.8vh;
}
.ContactWrapper__title {
  color: white;
  font-size: 4.5vh;
}
.ContactWrapper__subtitle {
  font-weight: 400;
  color: #cecece;
  font-size: 2vh;
  max-width: 25vw;
  width: 25vw;
  text-align: center;
  margin-top: 1vh;
  margin-bottom: 1vh;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__subtitle {
    width: 92vw;
    max-width: 92vw;
  }
}
.ContactWrapper__form {
  width: 52vh;
  height: 50vh;
  background-color: white;
  margin-top: 3vh;
  border-radius: 1.5vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__form {
    width: 90vw;
    height: 45vh;
  }
}
.ContactWrapper__form__label {
  color: #a8a8a8;
  font-weight: 400;
  font-size: 1.75vh;
  margin-left: 1.5vw;
  margin-top: 1.5vh;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__form__label {
    margin-left: 3vw;
  }
}
.ContactWrapper__form__input {
  width: 44vh;
  height: 4vh;
  transform: translateY(0.25vh);
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f8f8f8;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  box-sizing: border-box;
  margin-top: 0.25vh;
  margin-left: 1.5vw;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__form__input {
    width: 84vw;
    height: 4vh;
    transform: translateY(0.25vh);
    border-radius: 1vh;
    border: solid rgb(196, 196, 196) 0.1vh;
    background-color: #f8f8f8;
    font-size: 1.7vh;
    font-weight: 600;
    padding-left: 1vh;
    outline: none;
    box-sizing: border-box;
    margin-top: 0.25vh;
    margin-left: 3vw;
  }
}
.ContactWrapper__form__input::placeholder, .ContactWrapper__form__textarea::placeholder {
  color: rgb(173, 173, 173);
}
.ContactWrapper__form__input:focus, .ContactWrapper__form__textarea:focus {
  border-color: #333;
}
.ContactWrapper__form__textarea {
  width: 44vh;
  height: 4vh;
  transform: translateY(0.25vh);
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f8f8f8;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  box-sizing: border-box;
  margin-top: 0.25vh;
  margin-left: 1.5vw;
  height: 15vh;
  max-height: 15vh;
  max-width: 44vh;
  min-width: 44vh;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__form__textarea {
    width: 84vw;
    height: 4vh;
    transform: translateY(0.25vh);
    border-radius: 1vh;
    border: solid rgb(196, 196, 196) 0.1vh;
    background-color: #f8f8f8;
    font-size: 1.7vh;
    font-weight: 600;
    padding-left: 1vh;
    outline: none;
    box-sizing: border-box;
    margin-top: 0.25vh;
    margin-left: 3vw;
    max-width: 84vw;
    min-width: 84vw;
  }
}
.ContactWrapper__form__button {
  width: 55%;
  height: 10%;
  border-radius: 1vh;
  border: none;
  background-color: #333;
  color: white;
  font-weight: 600;
  font-size: 2vh;
  cursor: pointer;
  margin-left: 1.5vw;
  margin-top: 2vh;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__form__button {
    margin-left: 3vw;
  }
}
.ContactWrapper__form__button:hover {
  background-color: #555;
}
.ContactWrapper__form__names {
  width: 22vw;
  margin-left: 1.5vw;
  display: flex;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__form__names {
    width: 84vw;
    margin-left: 3vw;
  }
}
.ContactWrapper__form__names__nameInputSection {
  width: 44vh;
}
.ContactWrapper__form__names__nameInputSection__label {
  color: #a8a8a8;
  font-weight: 400;
  font-size: 1.75vh;
}
.ContactWrapper__form__names__nameInputSection__input {
  width: 21vh;
  height: 4vh;
  transform: translateY(0.25vh);
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f8f8f8;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  box-sizing: border-box;
  margin-top: 0.25vh;
}
@media screen and (orientation: portrait) {
  .ContactWrapper__form__names__nameInputSection__input {
    width: 40vw;
    height: 4vh;
    transform: translateY(0.25vh);
    border-radius: 1vh;
    border: solid rgb(196, 196, 196) 0.1vh;
    background-color: #f8f8f8;
    font-size: 1.7vh;
    font-weight: 600;
    padding-left: 1vh;
    outline: none;
    box-sizing: border-box;
    margin-top: 0.25vh;
  }
}
.ContactWrapper__form__names__nameInputSection__input::placeholder {
  color: rgb(173, 173, 173);
}
.ContactWrapper__form__names__nameInputSection__input:focus {
  border-color: #333;
}