.Wrapper {
  width: 100vw;
  height: 40vh;
  background-color: #1d1d1d;
  margin-top: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-content: center;
  padding-top: 5vh;
  padding-bottom: 5vh;
  overflow-x: hidden;
  background-image: radial-gradient(circle farthest-corner at 0% 50%, rgba(255, 1, 1, 0.15), transparent 47%), radial-gradient(circle farthest-corner at 40% 130%, rgba(122, 167, 255, 0.41), transparent 58%);
}
.Wrapper__title {
  text-align: center;
  color: white;
  font-size: 4vh;
}
@media screen and (orientation: portrait) {
  .Wrapper__title {
    font-size: 3.5vh;
  }
}
.Wrapper__text {
  text-align: center;
  color: white;
  font-size: 2.5vh;
  max-width: 30vw;
  font-weight: 400;
  width: 30vw;
  margin-left: 35vw;
}
@media screen and (orientation: portrait) {
  .Wrapper__text {
    width: 80vw;
    max-width: 80vw;
    font-size: 2.25vh;
    margin-left: 10vw;
  }
}
.Wrapper__form {
  width: 30vw;
  height: 5vh;
  margin-left: 35vw;
  display: flex;
  justify-content: space-evenly;
  align-content: center;
}
@media screen and (orientation: portrait) {
  .Wrapper__form {
    width: 85vw;
    margin-left: 7.5vw;
  }
}
.Wrapper__form__input {
  height: 5vh;
  width: 16vw;
  border-radius: 1.5vh;
  font-size: 1.75vh;
  padding-left: 1vw;
  border: none;
  outline: none;
  font-weight: 500;
  transition: border-color 250ms;
  will-change: border-color;
  border: solid transparent 0.1vh;
}
@media screen and (orientation: portrait) {
  .Wrapper__form__input {
    width: 48vw;
  }
}
.Wrapper__form__input::placeholder {
  color: gray;
}
.Wrapper__form__input:focus {
  border-color: #1d1d1d;
}
.Wrapper__form__button {
  width: 10vw;
  color: white;
  border: none;
  border-radius: 1.5vh;
  background-color: #525758;
  font-size: 2.1vh;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .Wrapper__form__button {
    width: 30vw;
  }
}
.Wrapper__form__button:hover {
  background-color: #343738;
}