.App {
    width : 100%;
    height : auto;
    overflow: hidden;
}

@keyframes simpleAppear {
    from {
        opacity : 0;
    }

    to {
        opacity : 1;
    }
}

@keyframes heroImage {
    from {
        transform: translateY(10vh);
    }

    to {
        transform: translateY(0vh);
    }
}