.wrapper {
  width: 100vw;
  height: auto;
  animation: simpleAppear 1s;
}
@media screen and (orientation: portrait) {
  .wrapper {
    height: auto;
  }
}
.wrapper__heading {
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20vw;
  padding-right: 20vw;
  padding-top: 5vh;
}
@media screen and (orientation: portrait) {
  .wrapper__heading {
    padding-left: 5vw;
    padding-right: 5vw;
  }
}
.wrapper__heading__title {
  font-size: 3vh;
}
.wrapper__heading__button {
  width: 12vw;
  height: 5vh;
  background-color: rgb(243, 241, 241);
  border: none;
  border-radius: 1.5vh;
  border: solid rgb(192, 192, 192) 0.1vh;
  color: rgb(95, 95, 95);
  font-size: 1.75vh;
  cursor: pointer;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .wrapper__heading__button {
    width: 15vh;
  }
}
.wrapper__heading__button:hover {
  background-color: rgb(219, 219, 219);
}
.wrapper__resourceCards {
  width: 100vw;
  height: auto;
  padding-top: 5vh;
  display: flex;
  justify-content: left;
  align-content: center;
  flex-wrap: wrap;
  padding-right: 20vw;
  padding-left: 20vw;
}
@media screen and (orientation: portrait) {
  .wrapper__resourceCards {
    height: auto;
    flex-direction: column;
    flex-wrap: nowrap;
    padding-left: 0;
    padding-right: 0;
    justify-content: space-around;
    margin-left: 5vw;
  }
}
.wrapper__resourceCards__card {
  width: 36vh;
  height: 75vh;
  cursor: pointer;
  display: block;
  margin: 1vh;
  transform: translateX(-1vh);
  transition: transform 250ms;
  will-change: transform;
}
@media screen and (orientation: portrait) {
  .wrapper__resourceCards__card {
    width: 90vw;
    height: 80vh;
  }
}
.wrapper__resourceCards__card__image {
  width: 100%;
  height: 50vh;
  object-fit: fill;
}
@media screen and (orientation: portrait) {
  .wrapper__resourceCards__card__image {
    width: 100%;
    height: 60vh;
  }
}
.wrapper__resourceCards__card__writtenBy {
  color: rgb(160, 160, 160);
  padding-top: 1.25vh;
  padding-bottom: 1.25vh;
  font-weight: 400;
  font-size: 1.75vh;
}
@media screen and (orientation: portrait) {
  .wrapper__resourceCards__card__writtenBy {
    text-align: center;
  }
}
.wrapper__resourceCards__card__title {
  margin: 0;
  font-size: 3vh;
  padding-bottom: 1.25vh;
}
@media screen and (orientation: portrait) {
  .wrapper__resourceCards__card__title {
    text-align: center;
  }
}
.wrapper__resourceCards__card__description {
  font-weight: 300;
  font-size: 1.6vh;
  color: rgb(43, 43, 43);
}
@media screen and (orientation: portrait) {
  .wrapper__resourceCards__card__description {
    text-align: center;
  }
}
.wrapper__resourceCards__card:hover {
  transform: translateY(-0.75vh) translateX(-1vh);
}
@media screen and (orientation: portrait) {
  .wrapper__resourceCards .left {
    margin-left: 0;
  }
}