.ErrorWrapper {
  width: 100vw;
  height: 90vh;
  margin-top: 8vh;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  animation: simpleAppear 650ms;
  padding-top: 22.5vh;
  padding-bottom: 22.5vh;
  background-image: radial-gradient(circle farthest-corner at 0% 50%, rgba(255, 1, 1, 0.15), transparent 47%), radial-gradient(circle farthest-corner at 40% 130%, rgba(122, 167, 255, 0.41), transparent 58%);
  background-color: #1d1d1d;
}
.ErrorWrapper__image {
  width: 16vh;
  animation: heroImage 400ms;
}
.ErrorWrapper__title {
  color: white;
  text-align: center;
  font-size: 4.6vh;
  font-weight: 600;
}
.ErrorWrapper__subtitle {
  color: rgb(226, 226, 226);
  font-weight: 400;
  font-size: 2vh;
  text-align: center;
  max-width: 22vw;
  width: 22vw;
}
@media screen and (orientation: portrait) {
  .ErrorWrapper__subtitle {
    width: 80vw;
    max-width: 80vw;
  }
}
.ErrorWrapper__button {
  background-color: white;
  color: black;
  border: none;
  width: 10vw;
  height: 5vh;
  border-radius: 2vh;
  font-size: 2vh;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .ErrorWrapper__button {
    width: 20vh;
  }
}
.ErrorWrapper__button:hover {
  background-color: aliceblue;
}