.Account {
  width: 100vw;
  height: 100vh;
  padding-top: 20vh;
  padding-bottom: 20vh;
  animation: simpleAppear 650ms;
  overflow-x: hidden;
  background-image: radial-gradient(circle farthest-corner at 0% 50%, rgba(255, 1, 1, 0.15), transparent 47%), radial-gradient(circle farthest-corner at 40% 130%, rgba(122, 167, 255, 0.41), transparent 58%);
  background-color: #1d1d1d;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.Account__redirect {
  text-align: center;
  text-decoration: none;
  font-weight: 600;
  color: rgb(199, 199, 199);
}
.Account__logo {
  width: 25vh;
}
.Account__form {
  width: 42vh;
  height: 40vh;
  background-color: white;
  border-radius: 2vh;
  padding-top: 3vh;
  padding-bottom: 3vh;
  animation: heroImage 400ms;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .Account__form {
    width: 90vw;
  }
}
.Account__form__title {
  font-size: 3vh;
}
.Account__form__subtitle {
  color: #555;
  font-weight: 350;
  font-size: 1.75vh;
}
.Account__form__input {
  width: 70%;
  height: 15%;
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f1f1f1;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  transition: border-color 250ms;
  will-change: border-color;
}
.Account__form__input::placeholder {
  color: rgb(173, 173, 173);
}
.Account__form__input:focus {
  border-color: #333;
}
.Account__form__button {
  width: 70%;
  height: 15%;
  border-radius: 1vh;
  border: none;
  background-color: #333;
  color: white;
  font-weight: 600;
  font-size: 2vh;
  cursor: pointer;
  transition: background-color 250ms;
  will-change: background-color;
}
.Account__form__button:hover {
  background-color: #555;
}
.Account__form__terms {
  display: flex;
  align-items: flex-start;
  padding-left: 15%;
  padding-right: 15%;
}
.Account__form__terms__label {
  font-size: 1.6vh;
  padding-left: 0.5vh;
  text-align: left;
}
.Account__form__paragraph {
  font-size: 1.6vh;
  color: #333;
}
.Account__form__paragraph__redirect {
  text-decoration: none;
  color: rgb(160, 160, 160);
  padding-left: 0.5vh;
  font-weight: 600;
}
.Account #register {
  height: 50vh;
}
.Account #register .Account__form__input, .Account #register .Account__form__button {
  height: 12%;
}
.Account #reset {
  height: 32vh;
}
.Account #reset .Account__form__input, .Account #reset .Account__form__button {
  height: 18%;
}
.Account #reset .Account__form__subtitle {
  font-size: 1.75vh;
}