.hero {
  width: 100vw;
  height: 50vh;
  margin-top: 8vh;
  background-color: #f5f6f7;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  animation: simpleAppear 650ms;
  overflow-x: hidden;
  background-image: linear-gradient(54deg, rgba(255, 131, 122, 0.25), rgba(255, 131, 122, 0) 28%), linear-gradient(241deg, rgba(239, 152, 207, 0.25), rgba(239, 152, 207, 0) 36%);
}
@media screen and (orientation: portrait) {
  .hero {
    flex-direction: column-reverse;
    align-content: flex-start;
    padding-left: 5vw;
    align-items: flex-start;
    height: 65vh;
  }
}
.hero__left, .hero__right {
  width: auto;
  height: auto;
}
.hero__left__title {
  font-size: 5.2vh;
  max-width: 25vw;
}
@media screen and (orientation: portrait) {
  .hero__left__title {
    font-size: 4.5vh;
    max-width: 90vw;
  }
}
.hero__left__paragraph {
  font-size: 2.25vh;
  color: #555;
  max-width: 25vw;
  font-weight: 400;
  margin-top: 2vh;
}
@media screen and (orientation: portrait) {
  .hero__left__paragraph {
    font-size: 2.6vh;
    max-width: 90vw;
  }
}
.hero__left__button {
  margin-top: 3vh;
  width: 24vh;
  height: 4.65vh;
  cursor: pointer;
  border: none;
  background-color: #222;
  color: white;
  font-size: 2.1vh;
  border-radius: 1.5vh;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .hero__left__button {
    width: 90vw;
  }
}
.hero__left__button:hover {
  background-color: #555;
}
.hero__right__image {
  width: 35vh;
  height: 35vh;
  animation: heroImage 400ms;
}
@media screen and (orientation: portrait) {
  .hero__right__image {
    width: 18vh;
    height: 18vh;
  }
}

.smaller {
  height: 30vh;
}