.Footer {
  width: 100vw;
  height: 40vh;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  align-content: center;
}
@media screen and (orientation: portrait) {
  .Footer {
    flex-direction: column;
    height: 90vh;
  }
}
.Footer__logoPart {
  width: 15vw;
  height: 20vh;
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  transform: translateY(-4vh);
}
@media screen and (orientation: portrait) {
  .Footer__logoPart {
    width: 80vw;
    height: 20vh;
    transform: translateX(7.5vw) translateY(-4vh);
  }
}
.Footer__logoPart__image {
  width: 25vh;
}
.Footer__logoPart__paragraph {
  transform: translateY(2vh);
}
.Footer__logoPart__rights {
  transform: translateY(4vh);
}
.Footer__logoPart__paragraph, .Footer__logoPart__rights {
  font-weight: 400;
  font-size: 1.6vh;
  color: #333;
}
.Footer__logoPart__paragraph__span, .Footer__logoPart__rights__span {
  color: #676;
}
.Footer__hyperlinks {
  width: 40vw;
  height: 40vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .Footer__hyperlinks {
    width: 100vw;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: left;
    height: 75vh;
    margin-top: 5vh;
  }
}
.Footer__hyperlinks__container {
  width: 9.6666666667vw;
  height: 18vh;
  display: flex;
  flex-direction: column;
  align-content: center;
  font-size: 1.6vh;
}
@media screen and (orientation: portrait) {
  .Footer__hyperlinks__container {
    width: 50vw;
    transform: translateX(7.5vw);
    font-size: 1.8vh;
  }
}
.Footer__hyperlinks__container__title {
  color: #999;
  font-weight: 600;
}
.Footer__hyperlinks__container__hyperlink {
  margin-top: 1.4vh;
  cursor: pointer;
  text-decoration: none;
  color: #555;
  transition: color 250ms;
  will-change: color;
}
.Footer__hyperlinks__container__hyperlink:hover {
  color: #222;
}