.GuideWrapper {
  width: 88vw;
  height: 1000vh;
  margin-left: 24vh;
  overflow: hidden;
}
@media screen and (orientation: portrait) {
  .GuideWrapper {
    margin-left: 0;
    height: 1100vh;
    margin-top: 10vh;
    width: 100vw;
  }
}
.GuideWrapper__copyright {
  padding-left: 5vh;
  padding-top: 12vh;
  font-size: 1.75vh;
}
.GuideWrapper__copyright__span {
  font-weight: 600;
  color: gray;
}
.GuideWrapper__header {
  width: 88vw;
  height: 24vh;
  background-color: rgb(240, 239, 239);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  padding-top: 4.5vh;
  padding-bottom: 4.5vh;
  padding-left: 5vh;
}
@media screen and (orientation: portrait) {
  .GuideWrapper__header {
    width: 100vw;
  }
}
.GuideWrapper__header__title {
  font-size: 4.2vh;
  font-weight: 600;
}
.GuideWrapper__header__paragraph {
  font-size: 1.8vh;
  font-weight: 400;
  color: #444;
  max-width: 40vw;
}
@media screen and (orientation: portrait) {
  .GuideWrapper__header__paragraph {
    max-width: 90vw;
  }
}
.GuideWrapper__section {
  padding-left: 5vh;
  width: 88vw;
  height: auto;
  overflow: hidden;
}
@media screen and (orientation: portrait) {
  .GuideWrapper__section {
    width: 100vw;
  }
}
.GuideWrapper__section__flex {
  width: 83vw;
  height: 30vh;
  padding-right: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.GuideWrapper__section__flex__button {
  width: 15vh;
  height: 6vh;
  font-size: 2vh;
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: rgb(204, 204, 204);
  border-bottom: solid rgb(204, 204, 204) 0.25vh;
  transition: border-color 250ms, color 250ms;
  will-change: border-color, color;
}
.GuideWrapper__section__flex .selected {
  width: 15vh;
  height: 6vh;
  font-size: 2vh;
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: rgb(5, 136, 243);
  border-bottom: solid rgb(5, 136, 243) 0.25vh;
}
.GuideWrapper__section__flex .selected:hover {
  color: rgb(5, 136, 243);
  border-bottom: solid rgb(5, 136, 243) 0.25vh;
}
.GuideWrapper__section__flex__button:hover {
  color: rgb(173, 173, 173);
  border-color: rgb(173, 173, 173);
}
.GuideWrapper__section__notification {
  padding: 3vh;
  background-color: rgb(209, 220, 223);
  text-align: center;
  width: 83vw;
  border-radius: 2vh;
  font-weight: 400;
  font-size: 2vh;
  color: #333;
  animation: notificationAppear 1s;
}
@keyframes notificationAppear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.GuideWrapper__section__form {
  height: 55vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-right: 5vh;
}
.GuideWrapper__section__form__button {
  width: 14vh;
  height: 5vh;
  margin-top: 3vh;
  cursor: pointer;
  border: none;
  border-radius: 1vh;
  background-color: #333;
  color: white;
  font-size: 2vh;
  font-weight: 600;
  transition: background-color 250ms;
  will-change: background-color;
}
.GuideWrapper__section__form__button:hover {
  background-color: rgb(61, 66, 68);
}
.GuideWrapper__section__form__input {
  width: 80vw;
  height: 6vh;
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f8f8f8;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  box-sizing: border-box;
  transition: border-color 250ms;
  will-change: border-color;
}
.GuideWrapper__section__form__select {
  width: 80vw;
  height: 6vh;
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f8f8f8;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  box-sizing: border-box;
  transition: border-color 250ms;
  will-change: border-color;
  color: #333;
  font-weight: 400;
  cursor: pointer;
}
.GuideWrapper__section__form__textarea {
  width: 80vw;
  height: 6vh;
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f8f8f8;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  box-sizing: border-box;
  transition: border-color 250ms;
  will-change: border-color;
  padding: 1vh;
  max-width: 80vw;
  min-width: 80vw;
  height: 15vh;
  max-height: 15vh;
  min-height: 15vh;
}
.GuideWrapper__section__form__input::placeholder, .GuideWrapper__section__form__textarea::placeholder {
  color: rgb(173, 173, 173);
}
.GuideWrapper__section__form__input:focus, .GuideWrapper__section__form__textarea:focus, .GuideWrapper__section__form__select:focus {
  border-color: #333;
}
.GuideWrapper__section__form__label {
  font-size: 2vh;
  color: #333;
  font-weight: 600;
  display: flex;
  align-items: center;
}
.GuideWrapper__section__form__label__input {
  margin-right: 1vh;
  cursor: pointer;
  width: 2vh;
  height: 2vh;
}
.GuideWrapper__section__customGrid__gridElement {
  display: block;
  width: 100%;
  height: 100%;
  background-color: #e7ecf0;
}
.GuideWrapper__section__buttonsContainer {
  width: 83vw;
  height: 38vh;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: flex-start;
}
.GuideWrapper__section__buttonsContainer__grid {
  width: 83vw;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(1, 1fr);
  justify-items: left;
  align-items: center;
}
.GuideWrapper__section__buttonsContainer__grid--label {
  width: 83vw;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(1, 1fr);
  justify-items: left;
  align-items: center;
  transform: translateY(2.5vh);
}
.GuideWrapper__section__buttonsContainer__grid--label__label {
  font-size: 2vh;
  color: #333;
  font-weight: 600;
}
.GuideWrapper__section__container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  width: 84vw;
  height: auto;
}
.GuideWrapper__section__divider {
  width: 70vw;
  transform: translateX(-5vh);
  margin-left: 9vw;
  height: 0.1vh;
  background-color: rgb(209, 209, 209);
  margin-top: 10vh;
  margin-bottom: 10vh;
}
.GuideWrapper__section__title {
  font-size: 3.6vh;
  font-weight: 500;
  color: #222;
  padding: 3vh;
  padding-left: 0vh;
  padding-bottom: 2vh;
}
.GuideWrapper__section__paragraph {
  font-size: 2vh;
  color: #555;
  font-weight: 400;
  padding: 3vh;
  padding-left: 0vh;
  padding-top: 0vh;
  max-width: 60vw;
}
@media screen and (orientation: portrait) {
  .GuideWrapper__section__paragraph {
    max-width: 85vw;
  }
}
.GuideWrapper__section__label {
  font-size: 2vh;
  color: #333;
  font-weight: 600;
}
.GuideWrapper__section__margLabel {
  font-size: 2vh;
  color: #333;
  font-weight: 600;
  display: block;
  margin-top: 1.5vh;
}
.GuideWrapper__section .smargin {
  display: block;
  margin-top: 12vh;
}
.GuideWrapper__section__largeH1 {
  font-size: 3.6vh;
  padding-top: 1vh;
  padding-bottom: 2.5vh;
}
.GuideWrapper__section__heading {
  padding-top: 1vh;
  padding-bottom: 2.5vh;
}
.GuideWrapper__section .lp {
  font-size: x-large;
}
.GuideWrapper__section .p {
  font-size: large;
}
.GuideWrapper__section__ptag {
  padding-top: 1vh;
  padding-bottom: 2.5vh;
}
.GuideWrapper__section__quote {
  border-left: pink 0.5vh solid;
  padding: 1vh 3vh;
  margin-top: 2vh;
  margin-bottom: 3vh;
  font-size: 1.75vh;
  max-width: 60vw;
}
@media screen and (orientation: portrait) {
  .GuideWrapper__section__quote {
    max-width: 85vw;
  }
}
.GuideWrapper__section__ul {
  margin-bottom: 3vh;
}
.GuideWrapper__section__ul__li {
  margin-left: 3vh;
  font-size: 1.9vh;
  margin-top: 1vh;
  color: #333;
}
.GuideWrapper__section__link {
  display: block;
  color: #777;
  text-decoration: none;
  font-size: 2.1vh;
  margin-top: 1vh;
  margin-bottom: 3vh;
}
.GuideWrapper__section__image {
  width: 40vh;
  margin-top: 1vh;
}
@media screen and (orientation: portrait) {
  .GuideWrapper__section__image {
    width: 35vh;
  }
}
.GuideWrapper__section__caption {
  margin-bottom: 3vh;
  font-size: 2vh;
  margin-top: 0.5vh;
}

.rich {
  font-size: 4.5vh;
}

.GuideNavbar {
  width: 24vh;
  height: 100vh;
  position: fixed;
  z-index: 1001;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  background-image: radial-gradient(circle farthest-corner at 0% 50%, rgba(94, 3, 3, 0.15), transparent 47%), radial-gradient(circle farthest-corner at 40% 130%, rgba(42, 56, 85, 0.41), transparent 58%);
  background-color: #1d1d1d;
}
@media screen and (orientation: portrait) {
  .GuideNavbar {
    width: 100vw;
    height: 10vh;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 5vw;
    padding-right: 5vw;
  }
}
.GuideNavbar__hamburger-menu {
  width: 8vh;
  height: 8vh;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 2vh;
  padding-bottom: 2vh;
  cursor: pointer;
  transition: background-color 250ms;
  will-change: background-color;
}
.GuideNavbar__hamburger-menu__stripe {
  width: 3vh;
  height: 0.45vh;
  background-color: white;
  border-radius: 1vh;
}
.GuideNavbar .hamburgerShown {
  background-color: rgb(18, 134, 211);
}
.GuideNavbar__image {
  width: 24vh;
  padding: 4vh;
}
@media screen and (orientation: portrait) {
  .GuideNavbar__image {
    width: 16vh;
    padding: 0vh;
  }
}
.GuideNavbar__links {
  width: 12vw;
  height: 42.5vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.GuideNavbar__links__link {
  padding-left: 2vw;
  text-decoration: none;
  color: white;
  font-size: 2vh;
  font-weight: 500;
  width: 12vw;
  height: 4.5vh;
  display: flex;
  align-items: center;
  transition: background-color 200ms;
  will-change: background-color;
}
.GuideNavbar__links__link:hover {
  background-color: #333;
}

.Slider {
  width: 100vw;
  height: auto;
  background-color: rgb(18, 134, 211);
  position: fixed;
  z-index: 1000;
  transform: translateY(-60vh);
  transition: transform 250ms;
  will-change: transform;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  padding-top: 10vh;
}
.Slider__link {
  text-decoration: none;
  color: white;
  font-size: 1.7vh;
  font-weight: 600;
  width: 100vw;
  height: 4.5vh;
  padding-left: 5vw;
  display: flex;
  align-items: center;
  transition: background-color 250ms;
  will-change: background-color;
}
.Slider__link:hover {
  background-color: #333;
}

.sliderShown {
  transform: translateY(0vh);
}