.PasswordWrapper {
  width: 100vw;
  height: 90vh;
  margin-top: 8vh;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  padding-top: 28vh;
  padding-bottom: 28vh;
  background-image: radial-gradient(circle farthest-corner at 0% 50%, rgba(255, 1, 1, 0.15), transparent 47%), radial-gradient(circle farthest-corner at 40% 130%, rgba(122, 167, 255, 0.41), transparent 58%);
  background-color: #1d1d1d;
}
.PasswordWrapper__title {
  color: white;
  text-align: center;
  font-size: 4.3vh;
  font-weight: 500;
  width: 26vw;
  max-width: 26vw;
}
@media screen and (orientation: portrait) {
  .PasswordWrapper__title {
    width: 90vw;
    max-width: 90vw;
  }
}
.PasswordWrapper__form {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  height: 17vh;
}
.PasswordWrapper__form__input {
  width: 20vw;
  height: 5vh;
  border-radius: 1vh;
  border: solid rgb(196, 196, 196) 0.1vh;
  background-color: #f1f1f1;
  font-size: 1.7vh;
  font-weight: 600;
  padding-left: 1vh;
  outline: none;
  box-sizing: border-box;
}
@media screen and (orientation: portrait) {
  .PasswordWrapper__form__input {
    width: 80vw;
  }
}
.PasswordWrapper__form__button {
  width: 20vw;
  height: 5vh;
  border-radius: 1vh;
  background-color: #5a5f63;
  border: none;
  color: white;
  font-size: 2vh;
  cursor: pointer;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .PasswordWrapper__form__button {
    width: 80vw;
  }
}
.PasswordWrapper__form__button:hover {
  background-color: #3f4246;
}